import React from 'react';

class PalCom extends React.Component
{
    constructor()
    {
        super();
    }
    render()
    {
        
        var condition = this.props.conditon;
        var number = this.props.number;
        //var callbck= ()=>{this.props.calbac(number)};
        
        var Bs1 = { backgroundColor: "", boderRadius: "", color:"", fontSize:"20px"};
        if(this.props.condition=="null")
        {Bs1.backgroundColor="white"; Bs1.borderRadius="0"; Bs1.color="red";}
        else if(this.props.condition=="visited")
        {Bs1.backgroundColor="Red";  Bs1.borderRadius="30%"; Bs1.color="white";}
        else if(this.props.condition=="answered")
        {Bs1.backgroundColor="Green"; Bs1.borderRadius="60%"; Bs1.color="white";}
        
        return(
        <button style={Bs1} onClick={()=>{this.props.calbac({number :number})}}>{number}</button>
        );
    }
}

export default PalCom;