import React from 'react';
import Header from './Header';
import Footer from './Footer';
import ExAr from './ExAr';
import Paper from './HTML.json';
import QuePal from './QuePal';
class Exam extends React.Component
{
    state ={que:[
        {no: "1" ,condition : "visited", answer : "null"},
        {no: "2" ,condition : "null", answer : "null"},
        {no: "3" ,condition : "null", answer : "null"},
        {no: "4" ,condition : "null", answer : "null"},
        {no: "5" ,condition : "null", answer : "null"},
        {no: "6" ,condition : "null", answer : "null"},
        {no: "7" ,condition : "null", answer : "null"},
        {no: "8" ,condition : "null", answer : "null"},
        {no: "9" ,condition : "null", answer : "null"},
        {no: "10" ,condition : "null", answer : "null"},
        {no: "11" ,condition : "null", answer : "null"},
        {no: "12" ,condition : "null", answer : "null"},
        {no: "13" ,condition : "null", answer : "null"},
        {no: "14" ,condition : "null", answer : "null"},
        {no: "15" ,condition : "null", answer : "null"},
        {no: "16" ,condition : "null", answer : "null"},
        {no: "17" ,condition : "null", answer : "null"},
        {no: "18" ,condition : "null", answer : "null"},
        {no: "19" ,condition : "null", answer : "null"},
        {no: "20" ,condition : "null", answer : "null"},
    ], number : 1};
    updateState1=(obj)=>
    {
        var obj1 = this.state;
        obj1.number = obj.number;
        for(var i in obj1.que)
        {
        
            if(i==(obj.number-1))
            {
                obj1.que[i].condition="visited";
                obj1.que[i].answer = "null";
            }
        }
        this.setState(obj1);
    }
    updateState2=(obj)=>
    {
        var obj1 =this.state;
        obj1.number = obj.number;
        for(var i in obj1.que)
        {
        
            if(i==(obj.number-1))
            {
                obj1.que[i].condition="visited";
                obj1.que[i].answer = "null";
            }
        }
        console.log("data",obj1);
        this.setState(obj1);
    }
    updateState3=(obj)=>
    {
        var obj1 = this.state;
        obj1.number = (obj.number+1);
        console.log(obj1.number);
        if(obj1.number>20)
        {obj1.number=1;}
        for(var i in obj1.que)
        {
            if((i)==(obj.number-1))
            {
                obj1.que[i].condition="answered";
                obj1.que[i].answer = obj.answer;
            }
        }
        this.setState(obj1);
    }
    constructor()
    {
        super();
        
    }   
    questate =[];
     prepareQuePal()
    {
        this.questate=[];
        for(var i in this.state.que)
        {
            var obj;
            obj = {condition : this.state.que[i].condition};
            
            this.questate.push(obj);
        }
        console.log(this.questate);
    }
    render()
    {
        this.prepareQuePal();
        var name;
        console.log(this.props.location.exam.name);
        switch(this.props.location.exam.name)
        {
            case "1": {name="HTML"; break;}
            case "2" : {name = "CSS"; break;}
            case "3" : {name="JavaScript"; break;}
            case "4" : {name="React"; break;}
        }
    return(

    <div>
        <Header/>
        <h1>{name} </h1>
        <h2>Question is {this.state.number}/20</h2>
        <div style={{display:"flex", flexDirection:"row", minHeight:"300px"}}>
        <ExAr num1={this.state.number} questate={this.questate} question={Paper.questions[this.state.number-1]} callback1={this.updateState3}/>
        <QuePal questate={this.questate} calback={this.updateState2}/>
        </div>
        <Footer/>
        </div>
    );
    }
}
export default Exam;