import React from 'react';

class ExAr extends React.Component
{
    state = {data: {answer : null, asta : false,bsta : false, csta : false, dsta :false} };
    i=1;
    constructor(props)
    {
        
        super(props);
        console.log(this.props);
        this.i=this.props.num1;
        
    }
   /*  componentWillUpdate()
    {
        setTimeout(()=>{this.setRadState(this.props.questate.answer);},100);
    }  */
    updateRadio(num)
    {
        console.log("change triggered");
        var obj = this.state;
        obj.data.asta=obj.data.bsta=obj.data.csta=obj.data.dsta=false;
        
        switch(num)
        {
            case 1: {obj.data.asta=true; obj.data.answer="a"; break;}
            case 2: {obj.data.bsta=true; obj.data.answer="b"; break;}
            case 3: {obj.data.csta=true; obj.data.answer="c"; break;}
            case 4: {obj.data.dsta=true; obj.data.answer="d"; break;}
        }
        this.setState(obj);
        console.log(this.state.data);
    }
    clearRes =()=>
    {
        var obj ;
        obj = this.state;
        obj.data.asta=obj.data.bsta=obj.data.csta=obj.data.dsta=false;
        this.setState(obj);
    }
    setRadState(ans)
    {
        var obj;
        obj=this.state;
        if(ans=='a')
        {obj.data.asta=true;}
        else if(ans=='b')
        {obj.data.bsta=true;}
        else if(ans=='c')
        {obj.data.csta=true;}
        else if(ans=='d')
        {obj.data.dsta=true;}
        this.setState(obj);
    }
    savenNext= ()=>
    {
        var obj={number : 1,answer : ""};
        obj.number=this.props.num1;
        obj.answer=this.state.data.answer;
        this.props.callback1(obj);
    }
checkQue()
{
    if(this.props.num1==this.i)
    return;
    else
    {
        var obj;
        obj=this.state;
        obj.data.asta=obj.data.bsta=obj.data.csta=obj.data.dsta=false;
        var ans=this.props.questate.answer;
        if(ans=='a')
        {obj.data.asta=true;}
        else if(ans=='b')
        {obj.data.bsta=true;}
        else if(ans=='c')
        {obj.data.csta=true;}
        else if(ans=='d')
        {obj.data.dsta=true;}
        this.setState(obj);
    }
    this.i=this.props.num1;
}
render(){
    setTimeout(()=>{this.checkQue();},10);
    var ques = this.props.question.q;
    var str = {checked: this.state.data.asta};
    var str2 = {checked: this.state.data.bsta};
    var str3 = {checked: this.state.data.csta};
    var str4 = {checked: this.state.data.dsta};
    //var tok; str4.checked?tok="true":tok="false";
    //console.log(str4);
    var options = [this.props.question.a,this.props.question.b,this.props.question.c,this.props.question.d];
    
    return(
        <div style={{minWidth:"60%"}}>
        <h3>{ques}</h3>
        <form>
        <ol>
        <li><input type="radio" value="a" name="ans"  onClick={()=>{this.updateRadio(1)}} {...str}/>{options[0]}</li>
        <li><input type="radio" value="b" name="ans"  onClick={()=>{this.updateRadio(2)}} {...str2}/>{options[1]}</li>
        <li><input type="radio" value="c" name="ans"  onClick={()=>{this.updateRadio(3)}} {...str3}/>{options[2]}</li>
        <li><input type="radio" value="d" name="ans"  onClick={()=>{this.updateRadio(4)}} {...str4}/>{options[3]}</li>
        </ol>
        </form>
        <div>
    <button onClick={this.clearRes}>Clear Response</button>
            <button onClick={this.savenNext}>Save and Next</button>
        </div>
        </div>
    );
 }

}

export default ExAr;
