import React from 'react';
import ReactDom from 'react-dom';
import Exam from './Exam';
import Header from './Header.js';
import Footer from './Footer.js';
import Java from './jav.jpg';
import Html from './OIP.jpg';
import Cs from './css.jpg';
import re from './react.jpg';
import Practise from './practise';
import {HashRouter as Router, Route, Link} from 'react-router-dom';

class Routo extends React.Component
{
    render()
    {
        return(        
            <Router>
            <Route exact path="/" component={IndPag}></Route>
            <Route exact path="/exam" component={Exam}></Route>
        </Router>);
    }
}

class IndPag extends React.Component
{
    render(){ return (
        <div style={{height: "100%"}}>
        <Header />
        <IndCon/>
        <Footer />
        
        </div> );}
}

class IndCon extends React.Component
{
    render(){ 
        return(
            <div style={{minHeight: "400px", backgroundColor:"beige"}}>
                <p style={{fontSize : "24px"}}>Select your examination</p>
                <div style={{display:"flex", flexDirection : "row"}}>
                <Card1 no="1"/>
                <Card1 no="2"/>
                <Card1 no="3"/>
                <Card1 no="4"/>
                </div>
            </div>
        );
    }
} 

class Card1 extends React.Component
{
    constructor()
    {
        super();
    }   
    render()
    {
        var k;
        var imgSrc;
        switch(this.props.no)
        {
            case "1": {k="HTML"; imgSrc=Html;break;};
            case "2": {k="CSS";imgSrc=Cs; break;};
            case "3": {k="JavaScript";imgSrc=Java; break;};
            case "4": {k="React";imgSrc=re; break;};
            default: k="none";
        }
        return(
            <div style={{textAlign:"center",minHeight: "280px",width:"20%",padding:"1%",margin:"1%",boxShadow:"1px 1px",border:"1px solid black", backgroundColor:"honeydew"}}>
                <div style={{marginLeft:"10%",width:"80%",height:"80%",overflow:"hidden"}}>

                    <img src={imgSrc} style={{height:"224px"}}></img>
                </div>
                <Link to={{pathname: "/exam", exam:{name:this.props.no}}}>{k}</Link>
            </div>
        );
    }
}
export default Routo;